import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import Modal from './Modal';

class Movies extends Component{
  constructor(props){
    super(props)
    this.input=React.createRef()
    this.state={
      isModal:false,
      scrollPosition:null
    }
  }
  modalToggle=()=>{
    this.setState({
      isModal:!this.state.isModal,
      scrollPosition:this.input.current.scrollTop
    },()=>{
      if(this.state.isModal){
          this.input.current.children[4].children[0].style.display="block"
          // console.log(this.state.scrollPosition)
          // window.scrollTo()
          document.querySelector("body").style.position="fixed"
        }else{
          this.input.current.children[4].children[0].style.display="none"
          document.querySelector("body").style.position="absolute"
        }
    })
  }

  render(){
    const {id,genres, poster,title,rating, summary}=this.props;
    return(
    <div ref={this.input} onClick={this.modalToggle} className="movieWrapper" key={id}>
       <img className='movie__poster' src={poster} alt={title}/>
       <div className='movie__title'>{title}</div>
       <div className='movie__genres'>
          <div>{genres.map((genre)=>`${genre} | `)}</div>
       </div>
       <div className='movie__rating'>{rating}</div>
        <div onClick={this.modalToggle}>
          <Modal 
            title={title}
            id={id}
            genres={genres}
            poster={poster}
            rating={rating}
            summary={summary}
            />
          </div>
    </div>
    )
  }
}


export default Movies;